<template>
  <div>
    <v-dialog persistent v-model="activate">
      <v-card>
        <v-card-title>
          <span class="headline">Cliente</span>
        </v-card-title>

        <v-divider></v-divider>

        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" s>
                <v-text-field
                  v-model="localCustomer.name"
                  label="Nome"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field
                  v-model="localCustomer.phone"
                  label="Telefone"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field
                  v-model="localCustomer.email"
                  label="Email"
                ></v-text-field>
              </v-col>
            </v-row>
            
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
          <v-spacer></v-spacer>
          <v-btn color="primary" v-if="saveEnabled"  @click="save"
            >Salvar</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>

   
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  name: "Customer",

  props: {
    customer: Object,
    activate: Boolean,
  },

  data() {
    return {
      localCustomer: { id: null, name: null, phone: null, email: null },
      saveEnabled: false,
    };
  },

  watch: {
    customer: function (val) {
      this.setLocalCustomer(val);

      this.saveEnabled = true;
    },
  },

  methods: {
    close() {
      this.$emit("update:activate", false);
    },

    setLocalCustomer(customer) {
      this.saveEnabled = false;

      let tmpCustomer = new Object();
      if (customer.id) {
        tmpCustomer.id = customer.id;
      }
      tmpCustomer.name = customer.name;
      tmpCustomer.phone = customer.phone;
      tmpCustomer.email = customer.email;

      this.localCustomer = tmpCustomer;
    },

    update() {
      var docRef = firebase
        .firestore()
        .collection("customer")
        .doc(this.customer.id);
      docRef.update(this.localCustomer);
      this.close();
    },

    add() {
      firebase.firestore().collection("customer").add(this.localCustomer);
      this.close();
    },

    save() {
      if (
        typeof this.localCustomer.id == "undefined" ||
        !this.localCustomer.id ||
        this.localCustomer.id.trim().length == 0
      ) {
        this.add();
      } else {
        this.update();
      }

      this.$emit("update:activate", false);
    },
  },
};
</script>